<template>
  <footer id="footer">
    <div class="line-segment"></div>
    <!-- social media links -->
    <!-- use a foreach to traverse the dataset -->
    <ul>
      <li v-for="i in srcList" v-bind:key="i.id">
        <img v-bind:src="i.imagesrc" v-on:click="openURL(i.link)" />
      </li>
    </ul>
  </footer>
</template>

<script>
const srcList = [
  {
    imagesrc: "icons/github.png",
    link: "https://github.com/KevinOfCathay",
  },
  {
    imagesrc: "icons/devpost.png",
    link: "https://devpost.com/KevinofCathay",
  },
  {
    imagesrc: "icons/twitter.png",
    link: "https://twitter.com/KevinZh45641809",
  },
];

export default {
  name: "Footer",
  methods: {
    openURL: function (URL) {
      window.open(URL, "_blank");
    },
  },
  data: function () {
    return { srcList };
  },
};
</script>

