<template>
  <div id="project-box" class="line" v-on:click="hideshowdetails()">
    <img
      v-for="imgsrc in imgsrcs"
      v-bind:key="imgsrc.id"
      class="project-img"
      v-bind:src="imgsrc"
    />
    <div style="margin-left: 12px">
      <div class="project-name">{{ projName }}</div>
      <div class="project-description">{{ description }}</div>
      <transition name="detail-trans">
        <p v-if="detail">{{ details }}</p>
      </transition>
    </div>
  </div>
</template>

<script>
export default {
  name: "ProjectItem",
  props: {
    imgsrcs: Array,
    projName: String,
    description: String,
    details: String,
  },
  methods: {
    hideshowdetails: function () {
      if (this.detail == false) {
        this.detail = true;
      } else {
        this.detail = false;
      }
    },
  },
  data: function () {
    return {
      detail: false,
    };
  },
};
</script>

<style scoped>
p {
  color: aliceblue;
  font-size: 16px;
}
#project-box {
  margin: 10px 0px 10px 0px;
  user-select: none;
  transition: 0.8s;
}

#project-box:hover {
  background-color: rgba(255, 248, 220, 0.3);
}

.line {
  display: flex;
}

.project-img {
  width: 180px;
  height: 120px;
  margin-right: 5px;
  border: 2px solid aliceblue;
}

.project-name,
.project-description {
  color: aliceblue;
}

.project-name {
  font-size: 18pt;
}
.project-description {
  font-size: 14pt;
}

.detail-trans-enter-active,
.detail-trans-leave-active {
  transition: all 0.8s;
}

.detail-trans-leave-to,
.detail-trans-enter {
  opacity: 0;
  color: black;
}
</style>